<script>import { onDestroy } from "svelte";
import { fade } from "svelte/transition";
import { Copy, Check } from "@gradio/icons";
let copied = false;
export let value;
let timer;
function copy_feedback() {
  copied = true;
  if (timer)
    clearTimeout(timer);
  timer = setTimeout(() => {
    copied = false;
  }, 2e3);
}
async function handle_copy() {
  if ("clipboard" in navigator) {
    await navigator.clipboard.writeText(value);
    copy_feedback();
  }
}
onDestroy(() => {
  if (timer)
    clearTimeout(timer);
});
</script>

<button
	on:click={handle_copy}
	title="Copy message"
	class:copied
	aria-label={copied ? "Message copied" : "Copy Message"}
>
	{#if !copied}
		<Copy />
	{:else}
		<span class="check">
			<Check />
		</span>
	{/if}
</button>

<style>
	button {
		position: relative;
		cursor: pointer;
		padding: 5px;
		width: 22px;
		height: 22px;
	}

	.check {
		top: 0;
		right: 0;
		z-index: var(--layer-top);
		background: var(--block-label-background-fill);
		width: 100%;
		height: 100%;
	}
</style>
